* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Ubuntu", sans-serif;
}

*::-webkit-scrollbar {
  width: 10px;
  background-color: #7773;
}

*::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #9999;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #999;
}

*::-webkit-scrollbar-track {
  background-color: #f8f9fa;
}

#root {
  background-color: #f8f9fa;
}

.blog-container {
  display: flex;
  padding-top: 5vh;
  min-height: 75vh;
  place-items: center;
  margin-bottom: 90px;
  flex-direction: column-reverse;
}

.blog-container .loader {
  width: 100%;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.blog-container .utilities {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.blog-container .utilities .account-alert {
  font-size: 1.2rem;
  font-weight: 500;
  margin-left: 2rem;
  text-align: center;
}

.blog-container .utilities .utility-item {
  margin-bottom: 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  cursor: pointer;
}

.blog-container .utilities .like:hover {
  color: #f33838;
}

.blog-container .utilities .liked {
  color: #f33838;
}

.blog-container .utilities .comment:hover {
  color: #368dd6;
}

.blog-container .blog {
  width: 60%;
  display: flex;
  align-self: center;
  justify-content: center;
}

.blog-container .blog .card {
  width: 100%;
  border: none;
  background-color: transparent;
}

.blog-container .blog .card .card-body {
  padding: 0.5rem;
}

.blog-container .blog .card .card-body h1 {
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  margin-top: 0.5rem;
}

.blog-container .blog .card .card-body .postHeader {
  display: flex;
  place-items: center;
  justify-content: space-between;
}

.blog-container .blog .card .card-body .postHeader .date {
  color: #777;
  font-weight: 400;
  font-size: 0.8rem;
  font-style: italic;
  margin-right: 1rem;
}

.blog-container .blog .card .card-body .postHeader .category {
  color: #fff;
  cursor: pointer;
  font-weight: 600;
  font-size: 0.65rem;
  margin-right: 1rem;
  border-radius: 15px;
  padding: 0.1rem 0.6rem;
  text-transform: uppercase;
  background-color: #368dd6;
}

.blog-container .blog .card .card-body .postHeader .share {
  border-radius: 50%;
  padding: 0.4rem 0.6rem;
  transition-duration: 0.5s;
}

.blog-container .blog .card .card-body .postHeader .share:hover {
  cursor: pointer;
  background-color: #aaa5;
}

.comment-box form button {
  width: 30%;
  color: #fff;
  display: flex;
  cursor: pointer;
  border-radius: 7px;
  align-items: center;
  margin-bottom: 2rem;
  padding: 0.5rem 1rem;
  justify-content: center;
  background-color: #368dd6;
}

.comment-box form button:hover {
  background-color: #257cc5;
}

.comment-container .comment {
  padding: 1rem 1rem;
  border-radius: 7px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.19);
}

.comment-container .comment .comment-author {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.2rem;
}

.comment-container .comment .comment-date {
  font-size: 0.8rem;
  font-weight: 400;
  color: #181717;
  margin-bottom: 0.5rem;
}

.comment-container .comment .comment-content {
  font-size: 1.2rem;
  font-weight: 500;
  color: #181717;
}

.copy-link .copy-link-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.copy-link .copy-link-container .url {
  font-size: 1rem;
  font-weight: 500;
  color: #d64531;
  word-break: break-all;
}

.copy-link .copy-link-container .copy {
  cursor: pointer;
  transition: 0.3s;
}

.copy-link .copy-link-container .copy:hover {
  color: #f33838;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.copy-link .copied-text {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 0.7rem;
  font-weight: 500;
}

.copy-link .copied-text span {
  border-radius: 7px;
  padding: 0.5rem 1rem;
  /* background-color: #d4b9e4; */
}

@media only screen and (max-width: 1024px) {
  .blog-container .blog {
    width: 70%;
  }
}

@media only screen and (max-width: 426px) {
  .blog-container .blog {
    width: 90%;
  }
}

.footer {
  left: 0;
  bottom: 0;
  width: 100%;
  cursor: default;
  padding: 30px 0;
  font-size: 15px;
  color: #fff;
  text-align: center;
  position: relative;
  background-color: #368dd6;
}

.footer .footerLogoLink {
  color: #fff;
  font-size: 1.1rem;
  font-weight: bold;
  text-decoration: none;
}

.footer .footerLogoLink:hover {
  cursor: pointer;
  text-decoration: underline;
}

* {
  font-family: "Ubuntu", sans-serif;
}

.navbar {
  margin-bottom: 1vh;
  background-color: #fff;
  border-bottom: 1px solid #4443;
}

.navbar .container .navbar-brand {
  cursor: pointer;
  color: #368dd6;
  font-size: 1.8rem;
  font-style: italic;
  font-weight: bolder;
}

.navbar .container .navbar-collapse {
  width: 100%;
  flex-grow: 0;
  justify-content: space-between;
}

.navbar .container .navbar-collapse .searchBar {
  width: 69%;
}

.navbar .container .navbar-collapse .searchBar form {
  width: 100%;
}

.navbar .container .navbar-collapse form input {
  width: 18vw;
  color: #333;
  margin-left: 2vw;
  background-color: #ffffff;
  border: 1px solid #368dd699;
}

.navbar .container .navbar-collapse form input:focus {
  outline: none;
  box-shadow: none;
}

.navbar .container .navbar-collapse form button {
  border: none;
  outline: none;
  background-color: #368dd6;
}

.navbar .container .navbar-collapse form button:hover {
  background-color: #146bb4;
}

.navbar .container .navbar-collapse .new-post {
  width: 100%;
  border: none;
  margin-right: 1vw;
  border-radius: 4px;
  background-color: #368dd6;
}

.navbar .container .navbar-collapse a {
  display: flex;
  place-items: center;
  justify-content: center;
}

.navbar .container .navbar-collapse .new-post:hover {
  background-color: #146bb4;
}

.navbar .container .navbar-collapse .user-icon {
  border-radius: 50%;
  background-color: #2e2d2e;
  border: 2px solid #a6a5a7;
  transition: all 0.3s ease-in-out;
}

.navbar .container .navbar-collapse .user-icon:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.navbar .container .navbar-collapse .dropdown-toggle::after {
  display: none;
}

.navbar .container .navbar-collapse .login {
  width: 100%;
  border: none;
  margin-right: 1vw;
  border-radius: 4px;
  background-color: #368dd6;
}

.navbar .container .navbar-collapse .login:hover {
  background-color: #146bb4;
}

@media only screen and (max-width: 768px) {
  .navbar .container .navbar-collapse .searchBar {
    width: 100%;
    margin-top: 2rem;
  }

  .navbar .container .navbar-collapse .searchBar input {
    margin-left: 0;
  }

  .navbar .container .navbar-collapse .login {
    margin-top: 1rem;
  }
}

.createpost-container {
  min-height: calc(100vh - 34px);
}

.createpost-container .container {
  display: flex;
  padding-top: 3vh;
  align-items: center;
  padding-bottom: 12vh;
  justify-content: center;
}

.createpost-container .container .card {
  width: 60%;
  border: none;
  padding: 1rem;
  border-radius: 7px;
  background-color: transparent;
}

.createpost-container .container .card .heading {
  font-size: 2.2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 2rem;
}

/* input type file */
.createpost-container .container .card form label {
  font-weight: 500;
  font-size: 1.1rem;
}

.createpost-container .container .card form .img-preview {
  width: 100%;
  height: 30vh;
  border-radius: 5px;
  margin-bottom: 1rem;
}

.createpost-container .container .card form .img-preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.createpost-container .container .card form .remove-image {
  width: 100%;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: medium;
  font-weight: bold;
  border-radius: 5px;
  background-color: #f44336;
}

.createpost-container .container .card form .remove-image:hover {
  background: #c9291e;
}

.createpost-container .container .card form input[type="file"] {
  width: 100%;
  border-radius: 5px;
  margin-bottom: 1rem;
  border: 1px solid #ddd;
}

.createpost-container .container .card form .postTitle {
  width: 100%;
  border: none;
  color: #222;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 3rem;
  background-color: transparent;
}

.createpost-container .container .card form .postTitle:focus {
  outline: none;
  box-shadow: none;
}

.createpost-container .container .card form .postTitle::-webkit-input-placeholder {
  color: #2224;
  font-size: 2rem;
  font-weight: bold;
}

.createpost-container .container .card form .postTitle:-ms-input-placeholder {
  color: #2224;
  font-size: 2rem;
  font-weight: bold;
}

.createpost-container .container .card form .postTitle::placeholder {
  color: #2224;
  font-size: 2rem;
  font-weight: bold;
}

.createpost-container .container .card form .ck-editor__editable {
  min-height: 25vh;
}

.createpost-container .container .card form .submit {
  width: 9rem;
  border: none;
  padding: 0.3rem;
  font-weight: 500;
  font-size: 1.1rem;
  border-radius: 4px;
  background-color: #368dd6;
}

.createpost-container .container .card .error {
  color: #f44336;
  margin: 1rem 0;
  font-size: 1rem;
  text-align: center;
}

@media only screen and (max-width: 786px) {
  .createpost-container .container .card {
    width: 100%;
  }

  .createpost-container .container .card form .submit {
    width: 100%;
  }
}

#selectForm {
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 1rem;
}

