  margin-bottom: 30px;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.home-container .container .explore #selectForm {
  margin: 0;
}

.home-container .container .explore {
  display: flex;
  margin-top: 4rem;
  margin-bottom: 2rem;
  place-items: center;
  justify-content: center;
}

.home-container .loader {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-container .blog-card {
  width: 90%;
  cursor: default;
  border-radius: 6px;
  margin-bottom: 40px;
}

.home-container .blog-card .card-img-top {
  height: 40vh;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}

.home-container .blog-card .card-body {
  padding: 1rem 1rem 2rem 1rem;
}

.home-container .blog-card .card-body h1 {
  font-weight: 700;
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
}

.home-container .blog-card .createdAt {
  display: flex;
}

.home-container .blog-card .createdAt .date {
  color: #777;
  font-weight: 400;
  font-size: 0.8rem;
  font-style: italic;
  margin-right: 1rem;
}

.home-container .blog-card .createdAt .category {
  color: #fff;
  cursor: pointer;
  font-weight: 600;
  font-size: 0.65rem;
  margin-right: 1rem;
