  padding: 0.1rem 0.6rem;
  text-transform: uppercase;
  background-color: #368dd6;
}

.home-container .blog-card .card-body .blog-items {
  display: flex;
  margin-top: 1rem;
  align-items: center;
  justify-content: flex-end;
}

.home-container .blog-card .card-body .blog-items .readMore {
  border: none;
  background-color: #368dd6;
}

.home-container .blog-card .card-body .blog-items .readMore:hover {
  background-color: #257cc5;
}

.home-container .latest-news {
  width: 79%;
  display: flex;
  flex-wrap: wrap;
  margin-top: 2rem;
  justify-content: space-between;
}

.home-container .latest-news div:nth-of-type(1) {
  flex-grow: 4;
}

.home-container .explore .resetCategory {
  cursor: pointer;
  border-radius: 4px;
  margin-left: 0.5rem;
  padding: 0.3rem 0.8rem;
  border: 1px solid #1111;
  transition-duration: 0.4s;
  background-color: #f5f5f5;
}

.home-container .explore .resetCategory:hover {
  background-color: #d5d5d5;
}

@media only screen and (max-width: 1024px) {
  .home-container {
    flex-direction: column;
